import { _decorator, Component, Node, Prefab, math, v3, instantiate, tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('GameManager')
export class GameManager extends Component {
    
    //障碍物
    @property(Prefab)
    private pref_Brick:Prefab = null!
    //障碍物生成节点
    @property(Node)
    private nd_BrickPlace:Node = null!


    //计时器
    private Timer = 0
    //所有砖块
    private allBricks:Node[] = []

    private summonBricks(){//生成砖块
        let rand = math.randomRange(-400,400);//随机数
        const newBrick=instantiate(this.pref_Brick);//实例化
        this.nd_BrickPlace.addChild(newBrick);//添加子节点
        newBrick.setPosition(v3(0,rand,0));//设置节点的相对位置
        this.allBricks.push(newBrick)//入队尾
        if(this.allBricks.length>8){//判断队列长度
            this.allBricks.shift().destroy()//队首出队并销毁
        }
        tween(newBrick)//移动
        .to(20,{position:v3(-2000,rand,0)})
        .delay(500)
        .start();
    }

    private checkTimer(){//每一定间隔生成砖块
        if(this.Timer<300){
            this.Timer++;
        }
        else{
            this.Timer=0;
            this.summonBricks();
            console.log("Summon")
        }
    }
    

    start() {
        this.summonBricks();
    }

    update(deltaTime: number) {
        this.checkTimer();
    }
}

